import { Environment, Milliseconds, SDKConfigurationType } from 'src/types';
import { ClientSettingsType } from './types';

export interface IClientSettings {
  readonly settings: ClientSettingsType;
}

export class ClientSettings implements IClientSettings {
  private updateInterval: number = 60 * Milliseconds.Minute;
  private environment?: Environment;
  private siteCode: string;

  constructor(siteCode: string, configuration?: Partial<SDKConfigurationType>) {
    this.siteCode = siteCode;

    if (configuration?.environment) {
      this.environment = configuration.environment;
    }

    if (configuration?.updateInterval) {
      if (configuration.updateInterval < 1) {
        this.updateInterval = 1 * Milliseconds.Minute;
      }

      this.updateInterval = configuration.updateInterval * Milliseconds.Minute;
    }
  }

  get settings(): ClientSettingsType {
    return {
      updateInterval: this.updateInterval,
      environment: this.environment,
      siteCode: this.siteCode,
    };
  }
}
